import React, { useEffect, useRef } from "react";
import { gsap, Linear } from "gsap";
import { useAppContext } from "../context";
import SectionBg from "../commonComponents/SectionBg";
import leftBg1 from "./img/topbg1.png";
import leftBg2 from "./img/topbg2.png";
import leftBg3 from "./img/topbg3.png";
const Top = (props) => {
  const { lang } = useAppContext();
  const titleRef = useRef(null);
  const titleChildren = useRef([]);
  const rightItemsRef = useRef(null);
  const rightItemListRef = useRef([]);

  useEffect(() => {
    gsap.set(titleChildren.current, { y: -10, opacity: 0 });
    const timeout = setTimeout(() => {
      gsap.to(titleChildren.current, {
        scrollTrigger: {
          trigger: titleRef.current,
          start: "50% 80%",
        },
        y: 0,
        opacity: 1,
        stagger: {
          each: 0.5,
          ease: Linear.easeNone,
        },
      });
    }, 500);
    return () => {
      clearTimeout(timeout);
    };
  }, []);

  useEffect(() => {
    gsap.set(rightItemListRef.current, { x: 10, opacity: 0 });
    const timeout = setTimeout(() => {
      gsap.to(rightItemListRef.current, {
        scrollTrigger: {
          trigger: rightItemsRef.current,
          start: "30% 80%",
        },
        x: 0,
        opacity: 1,
        stagger: {
          each: 0.5,
          ease: Linear.easeNone,
        },
      });
    }, 500);
    return () => {
      clearTimeout(timeout);
    };
  }, []);

  return (
    <div className={props.className}>
      <div className="item left" ref={titleRef}>
        <h2 ref={(e) => titleChildren.current.push(e)}>
          {textContent.left.title[lang]}
        </h2>
        <p ref={(e) => titleChildren.current.push(e)}>
          {textContent.left.text[lang]}
        </p>
      </div>
      <div className="item right" ref={rightItemsRef}>
        {textContent.right.map((d, i) => {
          return (
            <div
              key={`service-${i}`}
              ref={(e) => (rightItemListRef.current[i] = e)}
            >
              <SectionBg img={d.img} overlay={true} />
              <p>{d[lang]}</p>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default Top;

const textContent = {
  left: {
    title: {
      ru: "Услуги",
      en: "Services",
    },
    text: {
      ru: `За последние 5 лет ассортимент, производимой нами продукции, многократно увеличился. 
        Мы рады предложить Вам более 70 видов брусчатки и тротуарной плитки. А цветовая гамма поразит Ваше воображение.`,
      en: `For the last 5 years the variety of products we manufacture has raised multiple times.
        We are glad to offer you more than 70 sorts of paving stone and slabs. And the color spectrum amazes your imagination. `,
    },
  },
  right: [
    {
      ru: `Вибролитьевая и 
            вибропрессованная
            продукция`,
      en: "Vibrocast and vibropressed products",
      img: leftBg1,
    },
    {
      ru: `Железобетонных
          изделия`,
      en: `Reinforced concrete
          products`,
      img: leftBg2,
    },
    {
      ru: `Готовый бетон 
          всех марок 
          от М 100 до М 400`,
      en: `Ready concrete
          all brands
          from M 100 to M 400`,
      img: leftBg3,
    },
  ],
};
