import React, { useRef, useEffect } from "react";
import { gsap, Linear } from "gsap";
import { useAppContext } from "../context";
import SectionBg from "../commonComponents/SectionBg";
import leftBg from "./img/footerLeftBg.png";
import managerPhoto from "./img/manager.png";

const Footer = (props) => {
  const { lang, smallScreen } = useAppContext();
  const footerRef = useRef(null);
  const leftRef = useRef(null);
  const rightRef = useRef(null);

  useEffect(() => {
    if (!smallScreen) {
      const leftHeight = leftRef.current.offsetHeight;
      const rightHeight = rightRef.current.offsetHeight;
      footerRef.current.style.marginBottom = `-${
        Math.abs(rightHeight - leftHeight) / 2
      }px`;
    } else {
      footerRef.current.style.marginBottom = `0`;
    }
  }, [lang, smallScreen]);

  useEffect(() => {
    gsap.set(leftRef.current, { x: "200%" });
    gsap.set(rightRef.current, { x: "100%" });
    const timeout = setTimeout(() => {
      gsap.to(rightRef.current, {
        scrollTrigger: {
          trigger: footerRef.current,
          start: "50% 80%",
        },
        x: 0,
        duration: 0.3,
        ease: Linear.easeOut,
        onComplete: () => {
          gsap.to(leftRef.current, {
            x: 0,
            duration: 0.5,
            ease: Linear.easeOut,
          });
        },
      });
    }, 500);
    return () => clearTimeout(timeout);
  }, []);

  return (
    <div className={props.className} ref={footerRef}>
      <div className="item left" ref={leftRef}>
        <SectionBg img={leftBg} alt="desc" />
        <div className="text-wrp">
          <h5>Beton Stroy Detal</h5>
          <p>{textContent.left[lang]}</p>
        </div>
      </div>
      <div className="item right" ref={rightRef}>
        <img src={managerPhoto} alt="manager" />
        <div>
          <h5>{textContent.right.managerName[lang]}</h5>
          <p>{textContent.right.managerTitle[lang]}</p>
        </div>
      </div>
    </div>
  );
};

export default Footer;

const textContent = {
  left: {
    ru: ` Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
      eiusmod tempor incididunt ut labore et dolore magna aliqua RURURURU.`,
    en: ` Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
    eiusmod tempor incididunt ut labore et dolore magna aliqua.`,
  },
  right: {
    managerName: {
      ru: "Имя Фамилия",
      en: "Firstname Surname",
    },
    managerTitle: {
      ru: "Руководитель Beton Stroy Detal",
      en: "Head of Beton Stroy Detal",
    },
  },
};
