import React from "react";
import { MdLocalPhone } from "react-icons/md";

const Phone = (props) => {
  return (
    <a className={props.className} href={`tel:${phoneNumber.id}`}>
      <MdLocalPhone className="icon" /> <span>{phoneNumber.value}</span>
    </a>
  );
};

export default Phone;

export const phoneNumber = { id: "+712363333", value: "+71 236-33-33" };
